// create.component.js

import React, { Component } from 'react';
import axios from 'axios';
import ReactDOM from 'react-dom';
import TableRows from './TableRow';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles({
  root: {
    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
    border: 0,
    borderRadius: 3,
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    color: 'white',
    height: 48,
    padding: '0 30px',
  },
});



export default class Settings extends Component {

constructor(props) 
 {
    super(props);
    this.state = {business: [],cast_name:''};

    this.onChangeCastName=this.onChangeCastName.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
   // this.state={}
 }

 onChangeCastName(e)
 {
     this.setState({cast_name:e.target.value});
 }
 componentDidMount(){
    axios.get('http://localhost:4000/cast')
      .then(response => {
        this.setState({ business: response.data });
      })
      .catch(function (error) {
        console.log(error);
      })
  }
 onSubmit(e)
{
    e.preventDefault(e);
    const obj={
        cast_name:this.state.cast_name
    };
    axios.post('http://localhost:4000/cast/add', obj)
        .then(res => console.log(res.data));
    console.log(obj);
  //  this.componentDidMount();
    
    

    setTimeout(
        function() {
            this.componentDidMount();
        }
        .bind(this),
        1000
    );


    ReactDOM.render(<div className="alert alert-success alert-dismissible">
  <a href="#" className="close" data-dismiss="alert" aria-label="close">&times;</a>
  Cast Added Successfully
</div>,document.getElementById('alerts'));
   
   
}



  
  tabRow(){
      
    return this.state.business.map(function(object, i){
        console.log(object);
        return <TableRows obj={object} key={i} />;
    });
  }


  render() 
  {
    var alerts = "";
  

    alerts = "displayNone";
    return (

      
     
          <Grid container spacing={3} style={{ marginTop: 10 }}>
      
      <Grid item xs={12} sm={6} >
        <Paper > <div className="container">
        <br/>   <h3 >Add New Caste</h3>
            <div id="alerts" className={alerts} className=" alert-success" role="alert"></div>
           <form onSubmit={this.onSubmit}>
           

<TextField
        id="standard-with-placeholder"
        label="Cast Name"
        placeholder="Placeholder"
        value={this.state.cast_name}
          onChange={this.onChangeCastName}
        margin="normal"
      />
       <br/>
      <Button type="submit" name="submit" value="submit" variant="contained" color="primary" >
        Send
        {/* This Button uses a Font Icon, see the installation instructions in the docs. */}
        
      </Button> 
     
           </form>   
           <br/> <br/> 
        </div></Paper>
      </Grid>

      <Grid item xs={12} sm={6}>
        <Paper >   <div>
        <br/> <div id="remove"></div>  <h3 align="center">Cast List</h3>
          <table className="table table-striped" style={{ marginTop: 20 }}>
            <thead>
              <tr>
                <th>Cast Name</th>
                <th colSpan="2">Action</th>
              </tr>
            </thead>
            <tbody>
              { this.tabRow() }
            </tbody>
          </table>
        </div></Paper>
      </Grid>
      
    </Grid>
       

    )
  }


}