import React, { Component } from 'react';
import { createStore } from 'redux';
import axios from 'axios';
import { BrowserRouter as Router, Switch, Route, Link, NavLink } from 'react-router-dom';

import Create from './business/create.component';
import Edit from './business/edit.component';
import Index from './business/index.component';
import Settings from './cast/settings.component';
import Retrivecast from './cast/index.component';

import Userlist from './users/list.component';
import Useredit from './users/edit.component';


export default class LoginPage extends Component 
{

    constructor(props) 
    {
        super(props);
  
    
        this.onChangeUserName = this.onChangeUserName.bind(this);
        this.onChangePassword = this.onChangePassword.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {UserName: '',Password:''};

        
      }
  

    
      handleChange(event)
      {
       // this.setState({username: event.target.value,password: event.target.value });
       
      }
      onChangeUserName(e) {
        this.setState({
          UserName: e.target.value
  
        });
      }

      onChangePassword(e) {
        this.setState({
          Password:e.target.value
        });
      }
    
      handleSubmit(event)
      {
        console.log(this.state);
//alert('A name was submitted: ' + this.state.UserName+this.state.Password);
       this.props.history.push('/createuser')
        event.preventDefault();
      }



  render() 
  {
    
    return(  <div>    <div><div className="container-scroller">
    <div className="container-fluid page-body-wrapper full-page-wrapper">
      <div className="content-wrapper d-flex align-items-center auth auth-bg-1 theme-one">
        <div className="row w-100">
          <div className="col-lg-4 mx-auto">
            <div className="auto-form-wrapper">
              <form onSubmit={this.handleSubmit}>
                <div className="form-group">
                  <label className="label">Username</label>
                  <div className="input-group">
                    <input type="text" className="form-control" placeholder="Username" value={this.state.UserName} onChange={this.onChangeUserName} />
                    <div className="input-group-append">
                      <span className="input-group-text">
                        <i className="mdi mdi-check-circle-outline"></i>
                      </span>
                    </div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="label">Password</label>
                  <div className="input-group">
                    <input type="password" className="form-control" value={this.state.Password} onChange={this.onChangePassword}   placeholder="*********"/>
                    <div className="input-group-append">
                      <span className="input-group-text">
                        <i className="mdi mdi-check-circle-outline"></i>
                      </span>
                    </div>
                  </div>
                </div>
                <div className="form-group">
                  <NavLink to="./createuser" className="btn btn-primary submit-btn btn-block">Login</NavLink>
                </div>
                <div className="form-group d-flex justify-content-between">
                  <div className="form-check form-check-flat mt-0">
                    <label className="form-check-label">
                      <input type="checkbox" className="form-check-input" /> Keep me signed in </label>
                  </div>
                  <a href="#" className="text-small forgot-password text-black">Forgot Password</a>
                </div>
                <div className="form-group">
                  <button className="btn btn-block g-login">
                    <img className="mr-3" src="../../../assets/images/file-icons/icon-google.svg" alt=""/>Log in with Google</button>
                </div>
                <div className="text-block text-center my-3">
                  <span className="text-small font-weight-semibold">Not a member ?</span>
                  <a href="register.html" className="text-black text-small">Create new account</a>
                </div>
              </form>
            </div>
            <ul className="auth-footer">
              <li>
                <a href="#">Conditions</a>
              </li>
              <li>
                <a href="#">Help</a>
              </li>
              <li>
                <a href="#">Terms</a>
              </li>
            </ul>
            <p className="footer-text text-center">copyright © 2018 Bootstrapdash. All rights reserved.</p>
          </div>
        </div>
      </div>
     
    </div>
   
      </div></div> </div>);
  }

}