// create.component.js

import React, { Component } from 'react';
import axios from 'axios';
import ReactDOM from 'react-dom';


export default class Edit extends Component {
  constructor(props) {
    super(props);
    this.onChangeFirstName = this.onChangeFirstName.bind(this);
    this.onChangeLastName = this.onChangeLastName.bind(this);
    this.onChangeUserDob = this.onChangeUserDob.bind(this);
    this.onChangeUserAge = this.onChangeUserAge.bind(this);
    this.onChangeUserFile = this.onChangeUserFile.bind(this);
    this.onChangeUserCast = this.onChangeUserCast.bind(this);
    this.onChangeUserRelegion = this.onChangeUserRelegion.bind(this);
    this.onChangeUserCity = this.onChangeUserCity.bind(this);
    this.onChangeUserPhoto = this.onChangeUserPhoto.bind(this);

    this.onSubmit = this.onSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this)
    this.state = {
      FirstName: '',
      LastName: '',
      UserDob:'',
      UserAge: '',
      UserCast: '',
      UserRelegion:'',
      UserCity:'',
      UserPhoto:'',
      file: null,
      selectedFile: null,
      casts: []
    }
  }

 
  onChangeFirstName(e) {
    this.setState({
      FirstName: e.target.value
    });
  }
  onChangeLastName(e) {
    this.setState({
      LastName: e.target.value
    });
  }
  onChangeUserDob(e) {
    this.setState({
      UserDob: e.target.value
    })  
  }
  onChangeUserAge(e) {
    this.setState({
      UserAge: e.target.value
    })
  }

  onChangeUserFile(e) {
    this.setState({
      UserFile: e.target.value
    })
  }

  onChangeUserCast(e) {
    this.setState({
      UserCast: e.target.value
    })
  }

  onChangeUserRelegion(e) {
    this.setState({
      UserRelegion: e.target.value
    })
  }

  onChangeUserCity(e) {
    this.setState({
      UserCity: e.target.value
    })
  }

  onChangeUserPhoto(e) {
    this.setState({
      UserPhoto: ''
    })
  }
  onChangeHandler=event=>{
    this.setState({
      selectedFile: event.target.files[0],
      loaded: 0,
    })
  }

  handleChange(event) {
    this.setState({
      file: URL.createObjectURL(event.target.files[0]),
      selectedFile: event.target.files[0],
      loaded: 0,
    });
    console.log(event.target.files[0])

  }

  onClickHandler = () => {
    const data = new FormData()
    data.append('file', this.state.selectedFile)
    axios.post("http://localhost:4000/users/upload", data, { 
       // receive two    parameter endpoint url ,form data
   }).then(res => { // then print response status
     console.log(res.statusText)
  })
 }
 
  onSubmit(e) {
    e.preventDefault();
    const data = new FormData();
    const obj = {
      FirstName: this.state.FirstName,
      LastName: this.state.LastName,
      UserDob: this.state.UserDob,
      UserAge: this.state.UserAge,
      UserFile: this.state.UserFile,
      UserCast: this.state.UserCast,
      UserRelegion: this.state.UserRelegion,
      UserCity: this.state.UserCity,
     // UserPhoto:this.state.selectedFile
      UserPhoto:'test'
    };
    data.append('file', this.state.selectedFile)
    axios.post('http://localhost:4000/users/update/'+this.props.match.params.id, obj)
        .then(res => console.log(res.data));
    
   /* this.setState({
      FirstName: '',
      LastName: '',
      UserDob:'',
      UserAge: '',
      UserCast: '',
      UserRelegion:'',
      UserCity:'',
      UserPhoto:'',
      casts: []
    }); */
  /*  const data = new FormData()
    data.append('file', this.state.selectedFile)*/
   axios.post("http://localhost:4000/users/upload", data, { 
       // receive two    parameter endpoint url ,form data
   }).then(res => { // then print response status
     console.log(res.statusText)
  })
  ReactDOM.render(<div className="alert alert-warning success alert-dismissible">
  <a href="#" className="close" data-dismiss="alert" aria-label="close">&times;</a>
  User Updated Successfully
</div>,document.getElementById('alerts'));
  //  alert('Added Successfully');
 

  setTimeout(() => {
    this.props.history.push('/userlist');
  }, 1000);

  }
  componentDidMount() 
  {
    console.log(this.props.match.params.id);
   axios.get('http://localhost:4000/users/edit/'+this.props.match.params.id)
        .then(response => {
            this.setState({ 
              FirstName: response.data.FirstName,
              LastName: response.data.LastName,
              UserDob: response.data.UserDob,
              UserAge: response.data.UserAge,
              UserFile: response.data.UserFile,
              UserCast: response.data.UserCast,
              UserRelegion: response.data.UserRelegion,
              UserCity: response.data.UserCity,
              UserFile:  response.data.UserFile,
            
            });
            console.log(response.data.UserPhoto);

            this.setState({
              file: response.data.UserPhoto
            })
        })
        .catch(function (error) {
            console.log(error);
        }) 

        fetch("http://localhost:4000/cast")
        .then((responsed) => {
          return responsed.json();
        
        })
        .then(datad => {
    
        let cast_data = datad.map(cast => { return {display: cast} });
        console.log(datad);
       //  this.setState({ teams: [{value: '', display: '(Select your cast)'}].concat(teamsFromApi) });
      this.setState({ casts: cast_data });
          
        }).catch(error => {
          console.log(error);
        });


  }
  
  
  render() {
    return (
        <div style={{ marginTop: 10 }}>
            <h3>Edit User</h3>
            <form onSubmit={this.onSubmit} enctype="multipart/form-data">
                <div id="alerts"></div>
                <div className="row">

                <div className="col-sm-6">
                <div className="form-group">
                    <label>First Name:  </label>
                    <input 
                      type="text" 
                      className="form-control" 
                      value={this.state.FirstName}
                      onChange={this.onChangeFirstName}
                      />
                </div>

                <div className="form-group">
                    <label>Last Name: </label>
                    <input type="text" 
                      className="form-control"
                      value={this.state.LastName}
                      onChange={this.onChangeLastName}
                      />
                </div>

                <div className="form-group">
                    <label>Date of Birth: </label>
                    <input type="text" 
                      className="form-control"
                      value={this.state.UserDob}
                      onChange={this.onChangeUserDob}
                      />
                </div>
               
                

                <div className="form-group">
                    <label>Ageqqq: </label>
                    <input type="text" 
                      className="form-control"
                      value={this.state.UserAge}
                      onChange={this.onChangeUserAge}
                      />
                </div>


                <div className="form-group">
                    <label>File: </label>
                    <input type="file" 
                      className="form-control"
                      value={this.state.UserFile}
                      onChange={this.onChangeUserFile}
                      />
                </div>


                </div>
                
                
                <div className="col-sm-6">
                <div className="form-group">
                    <label>Caste:  </label>
                
<select 
                     className="form-control" 
                     value={this.state.UserCast}
                     onChange={this.onChangeUserCast}
                     
                      >
                        <option value="">Select Cast</option>
                      {this.state.casts.map((cast) => <option key={cast.display.cast_name} value={cast.display.cast_name}>{cast.display.cast_name}</option>)}
                      </select>
                </div>
                <div className="form-group">
                    <label>Religion: </label>
                    <input type="text" 
                      className="form-control"
                      value={this.state.UserRelegion}
                      onChange={this.onChangeUserRelegion}
                      />
                </div>
                <div className="form-group">
                    <label>City: </label>
                    <input type="text" 
                      className="form-control"
                      value={this.state.UserCity}
                      onChange={this.onChangeUserCity}
                      />
                </div> 
                
                <div className="form-group">
                    <label>Photo: </label>
                    <br/>
 
                   <img width="150" height="150" src={"/"+this.state.file} /><br/>
                    <input type="file" 
                      className="form-control"
                      onChange={this.handleChange}
                      value={this.state.UserPhoto}
                    
                      />
                </div>
               
                </div>




                 </div>
                <div className="form-group">
                    <input type="submit" value="Update" className="btn btn-primary"/>
                </div>
            </form>
        </div>
    )
  }
}