import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import axios from 'axios';
import DataTable from 'react-data-table-component';
import { tsExternalModuleReference } from '@babel/types';
import ReactDOM from 'react-dom';


 class UserList extends Component 
 {


  
  constructor(props) {
      super(props);
      this.state = {data: []};
  


      this.columns = [
        {
          name: 'First Name',
          selector: 'FirstName',
          sortable: true, 
        },
        {
          name: 'Last Name',
          selector: 'LastName',
          sortable: true,
         
        },
        {
          name: 'Date of Birth',
          selector: 'UserDob',
          sortable: true,
         
        },
        {
          name: 'Age',
          selector: 'UserAge',
          sortable: true,
        },
        {
          name: 'Cast',
          selector: 'UserCast',
          sortable: true,
         
        },
        {
          name: 'Relegion',
          selector: 'UserRelegion',
          sortable: true,
         
        }
        ,
        {
          name: 'City',
          selector: 'UserCity',
          sortable: true,
        },
        
        {
          name: 'Edit',
          button:true,
          cell: row =>  <Link  className="btn btn-primary" to={'/useredit/'+row._id} >Edit</Link>,
          ignoreRowClick:'no'
        },
        {
          name: 'Delete',
          button:true,
          selector: 'UserAge',
          cell: row =>  <button onClick={this.deleteUser} value={row._id}   className="btn btn-danger"> Delete</button>,
          ignoreRowClick:'no',
          data:'sdasdad'
        
      
        },
      ];
      

this.deleteUser = (data) => {
  
  axios.get('http://localhost:4000/users/delete/'+data.currentTarget.value)
   .then(console.log('Deleted')
   )
   .catch(err => console.log(err))
 
  
   ReactDOM.render(<div className="alert alert-danger alert-dismissible">
   <a href="#" className="close" data-dismiss="alert" onClick={clearalert} aria-label="close">&times;</a>
   User Removed Successfully
 </div>,document.getElementById('alerts'));
  
 this.componentDidMount();
  
 
 };
 
      const handleChange = (state) => {
        
      
        console.log('Selected Rows: ', state.selectedRows);
      
      
      
      };
      
      
      const clearalert = (state) => {
        ReactDOM.render('',document.getElementById('alerts'));
      
      
      };
    }

   

    componentDidMount(){
      axios.get('http://localhost:4000/users')
        .then(response => {
          this.setState({ data: response.data });

          console.log(response.data);
        })
        .catch(function (error) {
          console.log(error);
        })
    }

   

    render() {
      return (
       
        <div>
           <div id="alerts" ></div>
          <h3 align="center">User List</h3>
          <DataTable
       
        columns={this.columns}
        data={this.state.data}
        pagination={true}
        selectableRows 
        onRowClicked={this.handleChange}
        onTableUpdate={this.handleChange}
        
       
      />
        </div>
      );
    }
  }

 
  export default UserList;
  