import React, { Component } from 'react';  
import { Route } from 'react-router-dom';  
  
const MyLoginLayout = ({ children }) => (                         
    <div>  
      <p>This is the First Layout</p>  
      {children}                                       
    </div>  
  );  
  
  const MyLoginLayoutRoute = ({component: Component, ...rest}) => {  
    return (  
      <Route {...rest} render={matchProps => (  
        <MyLoginLayout>  
            <Component {...matchProps} />  
        </MyLoginLayout>  
      )} />  
    )  
  };  
  
export default MyLoginLayoutRoute; 