// TableRow.js

import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import ReactDOM from 'react-dom';


class TableRow extends Component {

  constructor(props) {
        super(props);
        this.delete = this.delete.bind(this);
    }
    componentDidMount(){
      axios.get('http://localhost:4000/cast')
        .then(response => {
          this.setState({ business: response.data });
        })
        .catch(function (error) {
          console.log(error);
        })
    }
    delete() {
        axios.get('http://localhost:4000/cast/delete/'+this.props.obj._id)
            .then(console.log('Deleted'))
            .catch(err => console.log(err))

            ReactDOM.render(<div className="alert alert-danger success alert-dismissible">
            <a href="#" className="close" data-dismiss="alert" aria-label="close">&times;</a>
            Cast Removed Successfully
          </div>,document.getElementById('remove'));
           setTimeout(
            function() {
              axios.get('http://localhost:4000/cast')
              .then(response => {
                this.setState({ business: response.data });
              })
              .catch(function (error) {
                console.log(error);
              })
            }
            .bind(this),
            1000
        );

    }
  render() {
    return (
        <tr>
          <td>
            {this.props.obj.cast_name}
          </td>
         
          <td>
            <Link to={"/edit/"+this.props.obj._id} className="btn btn-primary">Edit</Link>
          </td>
          <td>
            <button onClick={this.delete} className="btn btn-danger">Delete</button>
          </td>
        </tr>
    );
  }
}

export default TableRow;